$green: #9bc1bc

.square
	width: 13px
	height: 13px
	background: $green
	display: inline-block

.text-big
	font-size: 24px
	+med
	&_md
		font-size: 20px
	&_reg
		font-size: 22px
		line-height: 1.3
		+reg
	+r3
		font-size: 20px
		&_md
			font-size: 18px

.section-title
	font-size: 18px
	letter-spacing: 2.6px
	color: $green
	margin-bottom: 20px

.meta
	display: inline-block
	+reg
	font-size: 11px
	color: #b7b7b7
	+r2
		display: block

.divider
	height: 1px
	margin: 25px 0
	background: $green

// news
.l-news
	position: relative
	padding: 0 110px
	+clr
	&_border
		margin: 20px 0
		padding: 20px 0
		border-top: 1px solid $green
		border-bottom: 1px solid $green
	.l-col1
		float: left
		width: 70%
	.l-col2
		float: right
		padding-left: 30px
		width: 30%
	.l-col3
		padding: 10px 0
		clear: both
	+r1
		padding: 0 70px
		&_border
			margin: 20px 70px
			padding: 20px 0
		.l-col1
			width: 65.5%
		.l-col2
			width: 34.5%
	+r5
		padding: 0
		&_border
			margin: 20px 0
			padding: 20px 0
	+r2
		.l-col1,
		.l-col2
			width: 100%
			float: none
		.l-col2
			padding-left: 0
		.banner-img
			display: none

// soc-v
.soc-v
	position: absolute
	left: 0
	top: 0
	z-index: 10
	ul
		&.is-active
			padding-top: 90px
			+r(1150)
				padding-top: 0
	li
		text-align: center
		margin-bottom: 1px
		display: block
		a
			font-size: 29px
			color: #fff
			padding: 12px 10px
			background: #9bc1bc
			width: 50px
			height: 50px
			+tr(background .3s)
			&.fa-envelope
				font-size: 24px
			&.icon-vk, &.fa-odnoklassniki
				font-size: 24px
			.no-touch &
				&:hover
					background: darken(#9bc1bc, 10)
	+r5
		position: static
		clear: both
		+clr
		li
			float: left
			margin-right: 1px

// news
.news-header
	+reg
	&__subtitle
		margin-bottom: 10px
		font-size: 30px
		color: #9bc1bc
		+bold
	h1, .h1
		margin-bottom: 20px
	.text-big
		margin-bottom: 20px

.news-line
	margin: 0 -5px 40px
	&__info
		display: inline-block
		max-width: 80%
		vertical-align: middle
		color: #757575
	&__time
		display: inline-block
		color: #757575
		vertical-align: middle
		text-transform: uppercase
	span
		display: inline-block
		padding: 0 5px
		+light
	.btn
		display: inline-block
		vertical-align: middle
		margin-right: 20px
	+r3
		&__info
			display: block
			margin-top: 10px
		.btn
			margin-left: 0
	+r4
		+clr
		&__time
			float: right
			margin-top: 7px
			+ span
				display: block
				margin-top: 13px
				padding-left: 0
				clear: both

// sidebar
.banner-img
	padding: 50px 25px
	border: 1px solid #e6e6e6
	text-align: center
	margin-bottom: 20px
	background: #fff
	a
		line-height: 0
	img
		max-width: 100%
	+r2
		display: none

.item-sm
	font-size: 16px
	padding-bottom: 15px
	margin-bottom: 15px
	border-bottom: 1px solid #e6e6e6
	&:last-child
		border: 0
	+light
	p
		line-height: 1.2
	.text-big
		margin-bottom: 10px
		line-height: 1
	.no-touch &
		&:hover
			.text-big
				text-decoration: underline

.disqus
	display: block
	max-width: 100%

.note
	padding-top: 20px
	text-align: center
	border-top: 4px solid #9bc1bc
	&_mod
		margin-left: 108px
	span
		color: #9BC1BC
	+r2
		&_mod
			margin-left: 80px
	+r3
		margin-left: 0

.download
	display: block
	margin: 27px 0 35px
	padding: 13px 22px
	font-size: 18px
	color: #000
	border: 1px solid #9BC1BC
	position: relative
	.no-touch &
		&:hover
			&:before
				width: 100%
			.fa, span
				color: #fff
	&_mod
		margin-left: 108px
	&:before
		content: ""
		position: absolute
		top: 0
		bottom: 0
		left: 0
		width: 0
		transition: width 0.25s
		background: #9BC1BC
	.fa
		margin-right: 18px
		font-size: 24px
		color: #9BC1BC
	.fa, span
		vertical-align: middle
		position: relative
		z-index: 1
		transition: color 0.25s
	+r2
		margin-left: 80px
	+r3
		margin-left: 0
		padding: 13px
		text-align: center
		span
			display: block

.fullheight
	position: relative
	display: table
	width: 100%
	min-height: 100vh
	+cover
	&:before
		content: ""
		position: absolute
		top: 0
		left: 0
		right: 0
		bottom: 0
		+g(rgba(30, 18, 27, 0.3), rgba(27, 16, 24, 0.66))
	&__in
		position: relative
		height: 100vh
		display: table-cell
		padding: 100px 0 68px
		color: #FFF
		vertical-align: bottom
		z-index: 1
	&__bl
		max-width: 1290px
		margin: 0 auto
		padding: 0 110px
	&__el
		width: 70%
		&-mod
			width: 90%
	.btn
		background: #FFF
	.news-line
		margin: 0 0 22px
	.news-line__info
		color: #FFF
	.h1
		font-size: 90px
		line-height: 1
	.text-big
		font-size: 32px
		line-height: 1.1
	+r1
		&__bl
			max-width: 1100px
			padding: 0 70px
		&__el
			width: 65.5%
		.h1
			font-size: 70px
	+r5
		&__bl
			padding: 0 15px
	+r2
		min-height: 500px
		&__in
			height: auto
			padding-bottom: 10px
		&__bl
			max-width: 660px
		&__el
			width: 100%
		.h1
			font-size: 40px
		.text-big
			font-size: 20px
	&_special
		+r3
			min-height: 1px
		.fullheight
			&__in
				+r2
					padding: 50px 0 10px
		h1
			+r2
				font-size: 36px
		.text-big
			+r2
				font-size: 18px
				line-height: 1.2

// slider on inner pages
.inner-slider
	position: relative
	// margin-bottom: 15px
	.slick-dots
		margin-bottom: 20px
		li
			margin: 0 5px
			&.slick-active button
				background: #FFF
		button
			width: 12px
			height: 12px
			background: $mint
	&__prev,
	&__next
		position: absolute
		top: 0
		bottom: 0
		margin: 0
		font-size: 50px
		background: none
		color: $mint
		line-height: 1
		cursor: pointer
		transition: color 0.2s
		padding: 0 24px
		width: 50%
		.no-touch &
			&:hover
				i
					opacity: 1
		i
			opacity: 0
			transition: opacity 0.25s
			+r2
				opacity: 1
	&__prev
		left: 0
		text-align: left
	&__next
		right: 0
		text-align: right
	+r4
		&__prev,
		&__next
			font-size: 30px
			margin-top: -15px
		&__prev
			left: 15px
		&__next
			right: 15px
		.slick-dots
			margin-bottom: 10px
			button
				width: 8px
				height: 8px
	&__caption
		margin-bottom: 35px
		padding: 15px 0 0
		figure
			margin: 0
			outline: none
		figcaption
			padding: 0
	&__top
		position: relative

.list-links
	margin: 53px 0 43px
	&__bl
		position: relative
		display: block
		padding: 35px 15px
		background: rgba(#eff0ea, 0.5)
		border-bottom: 1px solid $mint
		transition: background 0.2s ease
		.no-touch &
			&:hover
				background: #eff0ea
		&:last-child
			border-bottom: none
	&__more
		margin-top: 15px
		+med
		font-size: 16px
		color: $mint
	.man
		max-width: 575px
		margin: 0 auto
		font-size: 18px
		+reg
		width: 100%
		p
			margin-bottom: 0
	.man__img
		vertical-align: top
	.man__text
		color: #000
		padding-right: 50px
		+r3
			padding-right: 0
	.h4
		+reg
	.fa-angle-right
		position: absolute
		top: 50%
		right: 30px
		margin-top: -32px
		font-size: 64px
		line-height: 1
		color: $mint
	+r3
		.man
			max-width: initial
		.fa-angle-right
			display: none

.expand
	position: relative
	margin-bottom: 20px
	&__img
		display: block
		max-width: 100%
		margin: 0 auto
	&__link
		position: absolute
		top: 20px
		right: 20px
		font-size: 30px
		color: #FFF
		line-height: 1
	+r4
		&__link
			top: 8px
			right: 8px
			font-size: 18px

.cut
	position: relative
	display: block
	float: left
	margin: 5px 30px 30px -110px
	padding: 23px 23px 25px 31px
	border: 1px dashed $mint
	+tr(background .3s)
	.no-touch &
		&:hover
			background: #EFF0EA
	&-ico
		position: absolute
		top: -1px
		left: -1px
		width: 29px
		height: 29px
		overflow: hidden
		&:before
			content: ""
			position: absolute
			width: 50px
			left: 0
			right: 0
			bottom: 0
			border-bottom: 1px dashed $mint
			transform: rotate(-45deg)
			transform-origin: 0 0 0
		&:after
			content: ''
			position: absolute
			+arr(28, 28, white, tl)

	.news-header
		max-width: 367px
		&__subtitle
			margin-bottom: 0
			font-size: 22px
	.h3
		+bold
	.fa
		position: absolute
		top: -11.5px
		left: -11px
		font-size: 23px
		color: #abcac5
		z-index: 1
	+r1
		margin-left: 0
		.news-header
			max-width: 300px
	+r3
		float: none
		.news-header
			max-width: initial
		.h3
			+med

.white-wrap
	background: #fff
	+clr
	.main-head
		margin-bottom: 0

.resume
	max-width: 1100px
	+r1
		max-width: 1100px
	&__form
		padding: 20px 0
		+r2
			padding: 0 0 20px
	&__row
		+clr
		border-top: 1px solid #d8d8d8
		padding: 30px 0 10px
		&:first-child
			border-top: none
	&__title
		clear: both
		font-size: 24px
		line-height: 1.3
		+med
		padding-bottom: 50px
		+r3
			padding-bottom: 30px
	&__right
		width: 31%
		padding-left: 30px
		+r2
			padding: 0 0 20px
	&__left
		width: 69%
	&__left, &__right
		float: left
		+r2
			float: none
			width: 100%
	.block-alert
		padding: 24px 7px 26px 26px
		p
			+bold
			font-size: 18px
			line-height: 1.7

.r-box
	padding-bottom: 20px
	+clr
	&_mod
		.r-box
			&__label
				width: 43%
				padding-right: 30px
				+r2
					padding-right: 0
			&__field
				width: 57%
				text-align: right
				+r2
					text-align: left
			&__label, &__field
				+r2
					width: 100%
	&__label
		width: 30%
		+med
		font-size: 17px
		padding-right: 15px
		+r2
			padding: 0 0 3px
	&__field
		width: 70%
		&_mod
			+clr
	&__label, &__field
		float: left
		+r2
			float: none
			width: 100%
	.input-full, .textarea-full
		font-size: 16px
		padding: 15px 15px 14px
		&::placeholder
			+reg
			color: #8e8e8e
		&_big
			font-size: 28px
			padding: 8px 15px 7px
	&__date
		font-size: 0
		+clr
	&__med
		width: 220px
	&__small
		width: 110px
	&__med, &__small
		padding-right: 30px
		float: left
		+r3
			float: none
			width: 100%
			padding: 0 0 20px
	p
		color: #9c9c9c
		font-size: 14px
		line-height: 1.3
		padding-top: 3px
	&__select
		width: 63%
		padding-right: 30px
		float: left
		+r3
			width: 100%
			padding-right: 0
			float: none
			margin-bottom: 20px
	&__checkbox
		float: left
		+r1
			float: none
			padding-top: 20px
			clear: both
		+r2
			float: none
	.btn-full
		max-width: 100%
		padding: 16px 15px 15px
		margin-top: 10px
.r-radios
	border-radius: 3px
	border: solid 1px #d8d8d8
	overflow: hidden
	font-size: 0
	display: inline-block
	vertical-align: top
	+r2
		border: none
		border-radius: 0
	&_mod
		.r-radio
			span
				padding: 13px 15px 12px
	.r-radio
		border-right: solid 1px #d8d8d8
		&:last-child
			border: none
			+r2
				border: solid 1px #d8d8d8
.r-radio
	background-color: #ffffff
	position: relative
	display: inline-block
	vertical-align: top
	cursor: pointer
	+r2
		border-radius: 3px
		border: solid 1px #d8d8d8
		margin: 0 15px 15px 0
	input
		position: absolute
		top: 0
		left: 0
		opacity: 0
		visibility: hidden
		&:checked + span
			background: #ff8c61
			color: #fff
	span
		font-size: 16px
		line-height: 1.2
		padding: 13px 41px 12px
		display: block
		transition: background 0.25s, color 0.25s
		min-width: 135px
		text-align: center
		+r1
			min-width: 120px
.upload-photo
	display: block
	width: 100%
	height: 365px
	background-color: #ffffff
	border: solid 1px #d8d8d8
	position: relative
	+vertical
	cursor: pointer
	+r2
		max-width: 300px
		margin: 0 auto
	input
		position: absolute
		top: 0
		left: 0
		opacity: 0
		visibility: hidden
	span
		display: inline-block
		vertical-align: middle
		color: #ff8c61
		i
			font-size: 120px
			display: block
			margin: 0 auto
		small
			font-size: 18px
			text-decoration: underline

.upload-file
	position: relative
	background-color: #ffffff
	border: solid 1px #d8d8d8
	display: block
	color: #ff8c61
	font-size: 16px
	line-height: 1.3
	padding: 15px 20px 13px
	cursor: pointer
	input
		position: absolute
		top: 0
		left: 0
		opacity: 0
		visibility: hidden
	i
		font-size: 18px
		float: left
		margin: 1px 10px 0 0
	span
		overflow: hidden

.r-checks
	font-size: 0
	margin-bottom: -20px
	li
		display: inline-block
		vertical-align: top
		padding: 0 30px 20px 0